<template>
  <Column class="ChannelVerify" y="s">
    <Column class="bg w-100b p-t-80 p-lr-60 " x="c" y="s">
      <img class="w-168 h-160 m-b-40" src="@/image/channelVerify/t-1.webp">
      <span class="font-54 bold m-b-20 text-center">{{ $t('channelVerify.officialVerification') }}</span>
      <span class="label-sm text-center c-info lh-36 m-b-60">{{ $t('channelVerify.pleaseVerify') }}</span>
      <el-input
        :modelValue="searchValue"
        class="w-100b h-88 search label m-b-40"
        :placeholder="$t('channelVerify.pleaseEnter')"
        :maxlength="100"
        @input="(e) => onChange(e)"
      >
        <template #prefix>
          <IconSvg name="icon-invite_search" class="label-lg c-placeholder m-r-12" />
        </template>
      </el-input>
      <el-button
        class="titls-sm w-100b m-b-280"
        type="primary"
        :loading="loading"
        :disabled="time"
        @click="query"
      >
        <span>{{ $t('table.query') }}</span>
        <span v-if="time">({{ time }})</span>
      </el-button>

      <Column class="w-100b label-sm c-info lh-36">
        <span>{{ $t('channelVerify.reminderT1') }}</span>
        <span>{{ $t('channelVerify.reminderT2') }}</span>
      </Column>
    </Column>
    <DiaRes v-model="diaResData.show" :type="diaResData.type" :typeText="diaResData.typeText" :searchValue="searchValue" />
  </Column>
</template>

<script setup>
import { DiaRes } from '@/view/channelVerify/components/'

const languageStore = useLanguageStore()

const props = defineProps({
  type: {},
  id: {},
})
let loading = $ref(false)

const diaResData = reactive({
  show: false,
  type: 0,
  typeText: '',
})

let searchValue = $ref()

let time = $ref(0)

const onChange = (v) => {
  const value = (v).replace(/\s/g, '')
  searchValue = value
}

const timing = () => {
  time = 10
  runTimer(() => {
    time--
    return time != 0
  }, 1000)
}

watch(() => time, (show) => {
  if (time == 0) {
  }
})

const query = async () => {
  try {
    if (isEmpty(searchValue)) {
      showNotify({ type: 'danger', message: $t('channelVerify.pleaseEnter') })
      return
    }

    loading = true
    const validateAccount = searchValue.replace(/(^\w+:|^)\/\//, '')
    const data = await homeApi.channelVerify({ validateAccount: validateAccount })
    if (data.channelTypeCn) {
      diaResData.type = 1
      diaResData.typeText = languageStore.locale == LANGCN ? data.channelTypeCn : data.channelTypeEn
    } else {
      diaResData.type = 0
    }
    diaResData.show = true
    console.log('data', data)
    timing()
  } finally {
    loading = false
  }
}

watch(() => diaResData.show, (show) => {
  if (show == false) {
    diaResData.typeText = ''
  }
})

onBeforeMount(() => {
  document.title = $t('channelVerify.officialVerification2')
})
</script>

