  .bg{
    min-height: calc(100vh);
    background-image: url("image/channelVerify/bg.webp");
    background-size: 100%;
    background-repeat: no-repeat;
    // background-position: center;
  }
  .search{
    .el-input__wrapper{
      padding-left: 20px !important;
      box-shadow: none !important;
    }
  }
}
</style>
